let fontSizeBase = global.fontSizeBase || 100;
let remFn = (val) => parseFloat( ((val / fontSizeBase)).toFixed(5)) + 'rem';
//支持textShadow

let textShadowNames = {
    textShadowOffset: true,
    textShadowRadius: true,
    textShadowColor: true
};
function fixTextShadow(style, result){

    let textShadow;
    if(style.textShadowOffset){
        let x = remFn(style.textShadowOffset.width || 0);
        let y = remFn(style.textShadowOffset.height || 0);
        textShadow = x + ' ' + y;
    }
    else{
        textShadow = '0rem 0rem';
    }
    let tsRadius = remFn(style.textShadowRadius || 0);
    textShadow += ' ' + tsRadius;
    let tsColor = style.textShadowColor ;
    if(tsColor){
        textShadow += ' ' + tsColor;
    }
    result.textShadow = textShadow;
}

let fixTextShadowObj= {
    names:textShadowNames,
    handle: fixTextShadow
}

export default fixTextShadowObj;
